import React from 'react';

import './DisabledCompanySelector.css';

class DisabledCompanySelector extends React.Component{
    render() {
        return(
            <div className="company-selector">
                <div className='AutoCompleteText'>
                    <input className="big-round" value={ this.props.companyName } type='text' readOnly/>
                </div>
            </div>
        )
    }
}

export default DisabledCompanySelector;
