import React from 'react';

import './CompanySelector.css';

class CompanySelector extends React.Component{
    constructor(props){
        super(props);
        this.state={
            companySuggestions: [],
            text: ''
        };
        this.holdNumberOnMouseOut=this.holdNumberOnMouseOut.bind(this);
    }

    onTextChanged = (e) => {
        const companyList = ["СК Рандеву", "СК МЕД-АСКЕР", "Страх-Трах"];
        const inputValue = e.target.value;
        let companySuggestions=[];
        if (inputValue.length>0) {
            const companyRegex = new RegExp(`^${inputValue}`,`i`);
            companySuggestions = companyList.sort().filter(v => companyRegex.test(v));
        } else {
            companySuggestions = companyList;
        }
        this.setState(() => ({companySuggestions, text: inputValue }));
    }

    showSuggestions() {
        const { companySuggestions } = this.state;
        if(companySuggestions.length===0) {
            return null;
        } else {
            return(
                <ul>
                    {companySuggestions.map((item)=><li onClick={() => this.suggestionSelect(item)}><div>{item}</div></li>)}
                </ul>
            );
        }
    }

    suggestionSelect(company) {
        this.setState(() => ({
            companySuggestions: [],
            text: company
        }));
        this.props.onChange(this.props.insuranceNumber, company, this.props.insuranceType);
    }

    onEnter = (e) => {
        let inputValue = e.target.value;
        if(e.keyCode===13) {
            this.setState(() => ({
                companySuggestions: [],
                text: inputValue
            }));
            this.props.onChange(this.props.insuranceNumber, inputValue, this.props.insuranceType);
        }
    }

    holdNumberOnMouseOut(e) {
        let newCompanyName = e.target.value;
        this.props.onChange(this.props.insuranceNumber, newCompanyName, this.props.insuranceType);
    }

    render() {
        const text = this.state.text;
        return(
            <div className="company-selector">
                <div className='AutoCompleteText'>
                    <input className="big-round" id="company" placeholder="Выберите компанию" value={ text }
                           type='text' onChange={this.onTextChanged} onClick={this.onTextChanged}
                    onMouseOut={this.holdNumberOnMouseOut}/>
                    {this.showSuggestions()}
                </div>
            </div>
        )
    }
}

export default CompanySelector;
