import React from 'react';

import './Form.css';
import InsuranceNumber from "../InsuranceNumber/InsuranceNumber";
import CompanySelector from "../CompanySelector/CompanySelector";
import TabsContainer from "../TabsContainer/TabsContainer";
import ServicesContainer from "../ServicesContainer/ServicesContainer";
import CheckButton from "../CheckButton/CheckButton";
import data from '../../services.json';
import NewInquiryButton from "../NewInquiryButton/NewInquiryButton";
import DisabledCompanySelector from "../DisabledCompanySelector/DisabledCompanySelector";

class Form extends React.Component{
    constructor(props){
        super(props);
        this.state={
            insuranceNumber: '',
            companyName: '',
            insuranceDate: '',
            companyPhone: '',
            insuranceType: 'ОМС',
            tagList: [],
            checkedTagList: [],
            insuranceIsFound: false
        };
        this.changeInsState = this.changeInsState.bind(this);
        this.getTagList = this.getTagList.bind(this);
        this.makeCheckedList=this.makeCheckedList.bind(this);
        this.toggleInsuranceIsFound=this.toggleInsuranceIsFound.bind(this);
        this.changePhoneAndDate=this.changePhoneAndDate.bind(this);
    }

    changeInsState(newNumber, newName, newType) {
        this.setState({
            insuranceNumber: newNumber,
            companyName: newName,
            insuranceType: newType
        });
    }

    toggleInsuranceIsFound(newState) {
        this.setState({
            insuranceIsFound: newState
        });
    }

    changePhoneAndDate(newPhone, newDate) {
        this.setState({
            insuranceDate: newDate,
            companyPhone: newPhone,
        })
    }

    getTagList(newList) {
        this.setState({
            tagList: newList
        });
    }

    makeCheckedList(newCheckedList) {
        this.setState({
            checkedTagList: newCheckedList
        });

    }

    render() {
        const checkButton = (<CheckButton tagList={this.state.tagList} services={data} companyName={this.state.companyName}
                                          insuranceNumber={this.state.insuranceNumber} isFound={this.state.insuranceIsFound}
                                          checkedTagList={this.state.checkedTagList} onClick={this.makeCheckedList} />);
        const newInquiryButton = (<NewInquiryButton clearTagList={this.getTagList} clearCheckedList={this.makeCheckedList}
                                                    clearInsurances={this.changeInsState} clearIsFound={this.toggleInsuranceIsFound}/>);
        const companySelector = (<CompanySelector companyName={this.state.companyName} insuranceType={this.state.insuranceType}
                                                  insuranceNumber={this.state.insuranceNumber} onChange={this.changeInsState}/>);
        const disabledCompanySelector = (<DisabledCompanySelector companyName={this.state.companyName}/>);
        return(
            <div className='card-content'>
                <div>
                    <TabsContainer insuranceIsFound={this.state.insuranceIsFound} insuranceType={this.state.insuranceType}/>
                    <div className="row-for-selectors">
                        <InsuranceNumber companyName={this.state.companyName} insuranceType={this.state.insuranceType}
                                         onChange={this.changeInsState} onFound={this.toggleInsuranceIsFound}
                                         onPhoneAndDate={this.changePhoneAndDate} phone={this.state.companyPhone}
                                         date={this.state.insuranceDate}/>
                        {this.state.insuranceIsFound ? disabledCompanySelector : companySelector}
                    </div>
                    <ServicesContainer tagList={this.state.tagList} checkedTagList={this.state.checkedTagList} services={data}
                                       onChange={this.getTagList}/>
                </div>
                {(this.state.checkedTagList.length===0) ? checkButton : newInquiryButton}
            </div>
        )
    }
}

export default Form;
