import React from 'react';

import './InsuranceNumber.css';
import data from "../../insurances";

class InsuranceNumber extends React.Component{
    constructor(props) {
        super(props);
        this.checkNewNumber = this.checkNewNumber.bind(this);
        this.holdUnknownNumber = this.holdUnknownNumber.bind(this);
        this.holdNumberOnMouseOut = this.holdNumberOnMouseOut.bind(this);
    }

    checkNewNumber(e) {
            const newInsNumber = e.target.value;
            let newCompanyName = '';
            let newInsuranceType = '';
            let newPhone ='';
            let newDate = '';
            for (let item = 0; item < 6; item++) {
                if (data[item]['sp'] === newInsNumber.toString()) {
                    newCompanyName = data[item]['sk'];
                    newInsuranceType = data[item]['ts'];
                    newPhone = data[item]['phone'];
                    newDate = data[item]['date'];
                    this.props.onChange(newInsNumber, newCompanyName, newInsuranceType);
                    this.props.onFound(true);
                    this.props.onPhoneAndDate(newPhone, newDate)
                    console.log(this.props.companyName, this.props.insuranceType);
                    break;
                }
            };
    }

    holdUnknownNumber(e) {
        if (e.keyCode===13) {
            let newInsNumber = e.target.value;
            this.props.onChange(newInsNumber, this.props.companyName, this.props.insuranceType);
            this.props.onFound(false);
        }
    }

    holdNumberOnMouseOut(e) {
            let newInsNumber = e.target.value;
            this.props.onChange(newInsNumber, this.props.companyName, this.props.insuranceType);
            this.props.onFound(false);
    }

    render() {
        return(
                <input type="text" className="round" placeholder="Введите номер полиса" onChange={this.checkNewNumber}
                       onKeyDown={this.holdUnknownNumber} onMouseOut={this.holdNumberOnMouseOut}/>
        )
    }
}

export default InsuranceNumber;
